import { Component } from 'react';
import Container from './Container';
import Footer from './Footer';
import './App.css';
import {getAllStudents} from './client';
import AddStudentForm from './forms/AddStudentForm';
import { errorNotification } from './Notification';
import {
    Table,
    Avatar,
    Spin,
    Space,
    Modal,
    Empty
} from 'antd';
import {LoadingOutlined} from '@ant-design/icons';

const getIndicatorIcon = () => <Space>
    <LoadingOutlined type="loading" style={{fontSize: 24}} spin></LoadingOutlined>
    </Space>;
class App extends Component{

    state = {
        students: [],
        isFetching: false,
        isAddStudentModalVisible: false,
    }


    componentDidMount() {
        this.fetchStudets();
    }

    openStudentModal = () => this.setState({isAddStudentModalVisible: true})

    closeStudentModal = () => this.setState({isAddStudentModalVisible: false})


    fetchStudets = () => {
        this.setState({
            isFetching: true
        });
        getAllStudents ()
        .then(res => res.json()
        .then(students => {
            //console.log(students);
            this.setState({
                students,
                isFetching: false
            });
        }))
        .catch((err) => {
            const message  = err.error.message;
            const description = err.error.httpStatus;
            errorNotification(message, description);
            this.setState({
                isFetching: false
            });
            
        });
    }

    render(){

        const {students, isFetching, isAddStudentModalVisible} = this.state;
        const commonElements = () => (
            <div>
               <Modal
                    title='Add new student'
                    open={isAddStudentModalVisible}
                    onOk={this.closeStudentModal}
                    onCancel={this.closeStudentModal}
                    width={1000}
                >
                    <AddStudentForm
                        onSuccess={() => {
                            this.closeStudentModal();
                            this.fetchStudets();
                        }}
                        onFailure= {(err) => {
                            const message = err.error.message;
                            const description = err.error.httpStatus;
                            console.log(JSON.stringify(err));
                            errorNotification(message,description);
                        }}
                    />
                </Modal>
                <Footer 
                    numberOfStudents={students.length}
                    handleAddStudentClickEvent={this.openStudentModal}
                ></Footer> 
            </div>
        )

        if(isFetching){
            return (
                <Container>
                    <Spin indicator={getIndicatorIcon()}></Spin>
                </Container>
            );
        }

        if(students && students.length){
    
            const columns = [
                {
                    title: '',
                    key: 'avatar',
                    render: (text, student) => (
                        <Avatar size='large'>
                            {`${student.firstName.charAt(0).toUpperCase()} 
                            ${student.lastName.charAt(0).toUpperCase()}`}
                        </Avatar>
                    )
                },

                {
                    title: 'Id',
                    dataIndex: 'id',
                    key: 'id'
                },
                {
                    title: 'First Name',
                    dataIndex: 'firstName',
                    key: 'firstName'
                },
                {
                    title: 'Last Name',
                    dataIndex: 'lastName',
                    key: 'lastName'
                },
                {
                    title: 'Email',
                    dataIndex: 'email',
                    key: 'email'
                },
                {
                    title: 'Gender',
                    dataIndex: 'gender',
                    key: 'gender'
                }
            ];

            return (
                <Container>
                    <Table
                        style={{paddingBottom: '100px'}} 
                        dataSource={students} 
                        columns={columns}
                        pagination={false} 
                        rowKey='id'/>
                    {commonElements()}    
                </Container>
                
            );
        }
        return (
            <Container>
                <Empty description ={
                    <h1>No Students found</h1> 
                }/>
                {commonElements()}    
            </Container>
        );
    }

    
}

export default App;
